.profile__name {
    font-family: 'Inter', 'Arial', sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 42px;
    line-height: 48px;
    color: white;
    box-sizing: border-box;
    padding-right: 18px;
    margin: 0;
    max-width: 311px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

@media (max-width: 1023px) {
    .profile__name {
        font-size: 27px;
        line-height: 33px;
        max-width: 207px;
    }
}

@media (max-width: 767px) {
    .profile__name {
        padding-right: 10px;
        text-align: center;
        max-width: 199px;
    }
}