.profile__button_type-add {
    background-image: url(../../../../images/add-button-new.svg);
    background-size: 22px;
    border: 2px solid white;
    border-radius: 2px;
    grid-area: add;
    align-self: center;
    padding: 14px 64px;
    width: 150px;
    height: 50px;
}


@media (max-width: 767px) {
    .profile__button_type-add {
        align-self: flex-end;
        padding: 17px 132px 17px 134px;
        margin-top: 36px;
        width: 282px;
    }
}