.popup__container {
    margin: 0;
    padding: 0;
    width: 430px;
    background-color: white;
    border-radius: 10px;
    position: relative;
}

@media screen and (max-width: 500px) {
    .popup__container {
        width: 300px;
    }
}

@media screen and (max-width: 320px) {
    .popup__container {
        width: 282px;
    }
}