.header {
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 880px;
    margin: 45px auto 40px auto;
    border-bottom: 1px solid;
    border-color: rgba(84, 84, 84, .7);
}

@media (max-width: 1023px) {
    .header {
        max-width: 581px;
    }
}

@media (max-width: 767px) {
    .header {
        max-width: 320px;
        margin: 28px auto 42px auto;
    }
}