.profile__image {
    width: 120px;
    height: 120px;
    border-radius: 50%;
}

.profile__image:hover {
    position: absolute;
    z-index: 0;
    opacity: .2;
}

@media (max-width: 1023px) {
    .profile__image {
        width: 100px;
        height: 100px;
        padding: 5px 16px;
    }
}

@media (max-width: 767px) {
    .profile__image {
        width: 120px;
        height: 120px;
    }
}