.profile__button_type-edit {
    background-image: url(../../../../images/edit-button-new.svg);
    background-size: 10px 10px;
    border: 1px solid white;
    padding: 7px;
    margin-top: 15px;
    width: 24px;
    height: 24px;
}

@media (max-width: 1023px) {
    .profile__button_type-edit {
        margin-top: 5px;
    }
}

@media (max-width: 767px) {
    .profile__button_type-edit {
        margin-top: 8px;
        padding: 5px;
        width: 18px;
        height: 18px;
    }
}