.popup__title {
    margin: 0;
    margin-top: 34px;
    margin-bottom: 48px;
    margin-left: 36px;
    max-width: 330px;
    font-family: "Inter", "Arial", sans-serif;
    font-weight: 900;
    font-size: 24px;
    line-height: 29px;
    color: black;
}

@media screen and (max-width: 500px) {
    .popup__title {
        max-width: 238px;
        font-size: 18px;
        line-height: 22px;
        margin: 0;
        margin-left: 23px;
        margin-top: 27px;
        margin-bottom: 75px;
    }
}