.popup__form-item {
    margin: 0;
    padding: 0;
    width: 358px;
    margin-right: 25px;
    box-sizing: border-box;
    align-self: center;
    margin-left: 35px;
    border: none;
    margin-bottom: 20px;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    color: black;
    display: block;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    padding-right: 20px;
    outline: none;
    padding-bottom: 9px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}

.popup__form-item:last-of-type {
    margin-bottom: 0px;
}

@media screen and (max-width: 500px) {
    .popup__form-item {
        width: 250px;
        margin-left: 27px;
    }
}

@media screen and (max-width: 320px) {
    .popup__form-item {
        width: 238px;
        margin-bottom: 31px;
        margin-left: 20px;
    }
}